@import url(https://fonts.googleapis.com/css?family=Poiret+One);
.App {
  text-align: center;
  font-family: 'Poiret One', cursive;
  font-weight: bold;
}

#drum-machine {
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  background:radial-gradient(ellipse at center, rgb(117, 117, 117) 0%, rgba(0,0,0,1) 100%);
  display: flex;
  align-items: center;
  width: 650px;
  border-radius: 25px;
}

#drum-container {
  margin: 50px;
  display: grid;
  grid-template: repeat(3, 100px) / repeat(3, 100px);
  grid-gap: 10px;
  justify-content: center;
  background: #c9b590;
  padding: 25px;
  border-radius: 20px;
}
#option-container {
  
  display: grid;
  grid-template: repeat(1, 90px) / repeat(1, 50px);
  justify-content: center;  
  margin: -10px;
}

.drum-pad {
  width: 100px;
  height: 100px;
  background-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(255,255,255,0.6) inset, 2px 2px 3px grey;
  font-size: 30px;
}

.playing {
  box-shadow: 0 0 2px 2px #3d3d3d inset;
  background-image: radial-gradient(ellipse at center, rgba(100,100,100,1) 0%, white 100%);
}

#display {
  background: #7c5202;
  width: 160px;
  padding: 15px;
  border-radius: 20px;
  box-shadow: 0 0 2px 2px #3d3d3d inset;
  color: gainsboro;
  font-size: 20px;
}
.toggle-wrapper {
  margin: 0.5em auto;
}

.toggle-wrapper label {
  padding: 0.3em;
  font-size: 17px;
  display: block;
}

.toggleContainer {
  width: 100%;
  position: relative;
  background: #ffffff;
  border: 1px solid #eeeeee;
}

.toggleContainer .toggleOption {
  display: inline-block;
  position: relative;
  z-index: 1;
  text-align: center;
  height: 40px;
  line-height: 40px;
  cursor: pointer;
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  color: #807b77;
  font-size: 1em;
}

.toggleContainer .toggleOption.selected {
  color: #ffffff;
  cursor: auto;
  cursor: initial;
}

.toggleContainer .toggle {
  position: absolute;
  height: 100%;
  bottom: 0;
  left: 0;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  background: #df5d5d;
}

.optionDisabled {
  background: #e6e6e6;
  cursor: no-drop !important;
}
.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}
